#recommendations[data-v-a90aa3be] {
	padding: 28px 30px 0 50px;
	margin-bottom: 20px;
	display: flex;
	height: 86px;
	overflow: hidden;
	flex-wrap: wrap;
	min-width: 0;
}
.recommendation-item[data-v-a90aa3be] {
	display: flex;
	align-items: center;
	flex-grow: 1;
	min-width: 250px;
}

/* show 2 per line for screen sizes smaller that 1200px */
@media only screen and (max-width: 1200px) {
#recommendations[data-v-a90aa3be] {
		height: initial;
		max-height: 189px;
}
.recommendation-item[data-v-a90aa3be] {
		flex-basis: 50%;
		max-width: calc(50% - 15px);
}
}

/*  GO FULL WIDTH BELOW 480 PIXELS */
@media only screen and (max-width: 480px) {
.recommendation-item[data-v-a90aa3be] {
		flex-basis: 100%;
		min-width: 100%;
}
}
